import { ref } from '@vue/composition-api'

export default {
  name: 'SmartModal',
  props: {
    title: {
      type: String,
      default: '默认标题'
    },
    content: {
      type: String,
      default: '默认内容'
    },
    maskColor: {
      type: String,
      default: 'rgba(0, 0, 0, 0.5)'
    },
    contentBgColor: {
      type: String,
      default: '#ffffff'
    }
  },
  setup() {
    const visible = ref(false)

    const showModal = () => {
      visible.value = true
    }

    const handleOk = () => {
      visible.value = false
    }

    const handleCancel = () => {
      visible.value = false
    }

    return {
      visible,
      showModal,
      handleOk,
      handleCancel
    }
  }
}
